'use strict';
import articles from '../../dist/data/articles.json';

document.addEventListener('DOMContentLoaded', () => {
	const searchInput = document.querySelector('.jsSearch');
	const navList = document.querySelector('.list');
	const allChapters = navList.innerHTML;
	const clearBtn = document.querySelector('.jsClearSearch');

	function findMatches(wordToMatch, articles){
		return articles.filter(article => {
			var regex = new RegExp(wordToMatch, 'gi');
			return article.content.match(regex) || article.title.match(regex);
		});
	}

	function renderNavigation() {
		if (searchInput.value === '') {
			return navList.innerHTML = allChapters;
		} else {
			var matchArray = findMatches(searchInput.value, articles);
			var html = matchArray.map(item => {
				var title = item.title;
				var slug = item.slug;

				return `<li class="list__item"><a href="${slug}.html">${title}</a></li>`;
			}).join('');
			navList.innerHTML = html;
		}
	}

	function clearSearch(e) {
		e.preventDefault();
		searchInput.value = '';
		renderNavigation();
	}
    
	searchInput.addEventListener('change', renderNavigation);
	searchInput.addEventListener('input', renderNavigation);

	clearBtn.addEventListener('click',clearSearch);    
});