'use strict';

function setAttributes(el,attrs){
	for (const [key,value] of Object.entries(attrs)){ 
		el.setAttribute(key,value);
	}
}

document.addEventListener('DOMContentLoaded', () => {
	const sidebar = document.querySelector('#sidebar');
	const siteBody = document.querySelector('.site__main');
	const site = document.querySelector('.site');
	const buttonToggleMenu = document.querySelector('.jsToggleMenu');

	let touchstartX = 0;
	let touchendX = 0;
    
	function toggleMenu() {
		// add/remove instead of toggle because of localStorage
		let expanded,label,hidden;
        
		if(config.menuClosed === 'true'){
			sidebar.classList.add('hidden');
			site.classList.add('menu-closed');
			expanded,label,hidden = 'false','Otwórz Menu','true';
		}
        
		if(config.menuClosed === 'false'){
			sidebar.classList.remove('hidden');
			site.classList.remove('menu-closed');
			expanded,label,hidden = 'true','Zamknij Menu','false';
			sidebar.focus(); 
		}   
        
		setAttributes(buttonToggleMenu,{'aria-expanded':expanded,'aria-label':label});
		sidebar.setAttribute('aria-hidden',hidden);
	}

	function handleClick(){
		config.menuClosed = config.menuClosed === 'true' ? 'false' : 'true';
		toggleMenu();   
		localStorage.setItem('app_config',JSON.stringify(config));
	}
    
	function handleSwipe() {
		if (touchstartX - touchendX >= 130) {
			config.menuClosed = 'true';
		}

		if (touchendX - touchstartX >= 130) {
			config.menuClosed = 'false';
		}
		localStorage.setItem('app_config',JSON.stringify(config));
	}
        
	const ls = localStorage.getItem('app_config');

	const config = ls ? JSON.parse(ls) : {
		menuClosed : false,
		fontSize : false,
		darkTheme : false
	};

	buttonToggleMenu.addEventListener('click', handleClick);
    
	siteBody.addEventListener('click', () => {
		if (window.innerWidth <= 750 && config.menuClosed === 'false') { // mobile devices, closing only
			handleClick();
		}
	});
    
	site.addEventListener('touchstart', (e) => {
		touchstartX = e.changedTouches[0].screenX;
	}, false);

	siteBody.addEventListener('touchend', (e) => {
		touchendX = e.changedTouches[0].screenX;
		handleSwipe();
	}, false); 
    
	toggleMenu();
    
});

