'use strict';

document.addEventListener('DOMContentLoaded', function() {
	const site = document.querySelector('.site');
	const buttonToggleFontSize = document.querySelector('.jsToggleFontSize');
	const buttonToggleTheme = document.querySelector('.jsToggleTheme');
    
	function handleClick(configParam,cl,btn){
		configParam = configParam === 'true' ? 'false' : 'true';
		toggle(configParam,cl,btn);
		localStorage.setItem('app_config', JSON.stringify(config));
	}
    
	function toggle(configParam,cl,btn){
		if(configParam === 'true'){
			site.classList.add(cl);
			btn.classList.add('btn--on');
		}else {
			site.classList.remove(cl);
			btn.classList.remove('btn--on');
		}
	}
    
	const ls = localStorage.getItem('app_config');
	const config = ls ? JSON.parse(ls) : {
		menuClosed : false,
		fontSize : false,
		darkTheme : false
	};
    
	buttonToggleFontSize.addEventListener('click',() => {
		handleClick(config.fontSize,'big-font',this);    
	});
    
	toggle(config.fontSize,'big-font',buttonToggleFontSize);
    
	buttonToggleTheme.addEventListener('click',() =>{
		handleClick(config.darkTheme,'dark-theme',this);
	});
	toggle(config.darkTheme,'dark-theme',buttonToggleTheme);
});